import { Component, OnInit, Injector, Renderer2, HostListener } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';
import { MatDialogRef } from "@angular/material/dialog";

import { SharedService } from '../shared/services/shared.service';
import { ModalService } from '../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.scss']
})
export class UserComponent implements OnInit {

  isSidebarMini:boolean = false;

  isApartmentSelected:boolean = false;

  modalService:any;

  constructor(
  private router: Router,
  private route: ActivatedRoute, 
  private injector: Injector,
  r: Renderer2, 
  private sharedService:SharedService,
  private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  showMiniSideBar(){
    return this.isSidebarMini ? 'mini' : '';
  }

  ngOnInit() {
    
    if(this.cookieService.get('apartmentId') != ""){
      this.sharedService.setApartmentSelected(true);
    }
    else {
     this.sharedService.setApartmentSelected(false);
     this.modalService.showUserApartmentModal();
    }

  	this.sharedService.sidebarminicast.subscribe( isSidebarMini => this.isSidebarMini = isSidebarMini );

  }

}
